body{
  background-color: #1A1A40;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App > div{
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.container > svg{margin-top:1em;}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.masteryCardContainer{margin: 1em; display:grid; grid-template-columns: repeat(3, minmax(100px, 33%)); grid-gap: 1em;}
.masteryCard{background-color: #2a303b; display:flex; flex-direction: column; justify-content: flex-start; text-align: left; border-radius: 5px;}
.masteryCard img{width: 100%;} 
.masteryCard div{padding: 0 1em;}
.rotate-in-center {
	-webkit-animation: rotate-in-center 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) infinite both;
	        animation: rotate-in-center 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) infinite both;
}
